import logo from './logo.svg';
import './App.css';
import * as tf from '@tensorflow/tfjs';
import * as loader from './loader';

const bucket_url = 'https://storage.googleapis.com/ml_bucket_ijd1/';

class Model {
  async init(url) {
	this.model = await loader.loadModelFromUrl(url);
	return this;
  }
}

async function printSummary() {
  const model = await new Model().init(bucket_url + 'models_js/model.json');
  model.model.summary()
}



function App() {
  return (
    <div className="App">
      <header className="App-header">
		<p>
          This is text that appears on the screen.
        </p>
		<button onClick={() => printSummary()}>Summary</button>
      </header>
    </div>
  );
}

export default App;
